<template>
  <div id="main">
    <div class="content page-order-checkout checkout">
      <div class="top">
        <div class="ad">编写收获地址</div>
        <ul class="cun" v-for="(item,index) in addressData " :key="index">
          <span @click="n=index"  ></span>
          <li>{{item.username}}</li>
          <li>
            {{item.loc[0].text}}
            {{item.loc[1].text}}
            {{item.loc[2].text}}
          </li>
          <li>{{item.address}}</li>
          <li>{{item.phone}}</li>
        </ul>

        <Adress v-if="isShow" @pop-close="handleRealClose"   :editum="eNum"  ></Adress>

        <div class="adbut">
       
          <div  @click="handleShow(n)" >修改地址</div>
          <div @click="handleShow(-1)">添加地址</div>
          <div>删除地址</div>
        </div>
      </div>
      <div class="gray-box">
        <div class="title pre-title">
          <h2>购物清单</h2>
        </div>
        <div class="box-inner ui-goods-cart">
          <div class="gray-sub-title cart-table-title">
            <span class="name">商品名称</span>
            <span class="subtotal">小计</span>
            <span class="num">数量</span>
            <span class="price">单价</span>
          </div>
          <div class="cart-table">
            <div class="cart-group js-cart-group">
              <div class="cart-items" v-for="(item,index) in checkCartData" :key="index">
                <div class="items-thumb">
                  <a href="javascript:;" target="_blank">
                    <img :src="item.image" />
                  </a>
                </div>
                <div class="name hide-row">
                  <div class="name-cell">
                    <a
                      href="javascript:;"
                      title="坚果 Pro 钢化玻璃手感膜 开孔 (后壳用)（黑色）"
                      target="_blank"
                    >{{item.title}}</a>
                  </div>
                </div>
                <div class="subtotal">
                  <div class="subtotal-cell">¥ {{item.cnum*item.price}}</div>
                </div>
                <div class="goods-num">{{item.cnum}}</div>
                <div class="price">¥ {{item.price}}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="box-inner">
          <div class="order-discount-line">
            <p>
              商品总计：
              <span>¥ {{allInfo.checkPrice}}</span>
            </p>
          </div>
        </div>
        <div class="box-inner">
          <div class="last-payment clear">
            <span class="jianguo-blue-main-btn big-main-btn payment-blue-bt fn-right js-checkout">
              <a>提交订单</a>
            </span>
            <span class="prices fn-right">
              应付金额：
              <em>¥ {{allInfo.checkPrice}}</em>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Adress from "@/components/Adress";

export default {
  data() {
    return {
      isShow: false,
      n:0,
      eNum:-1
    };
  },
  computed: {
    checkCartData() {
      return this.$store.state.cartData.filter(obj => {
        return obj.isCheck;
      });
    },
    allInfo() {
      return this.$store.getters.allInfo;
    },
    addressData() {
      return this.$store.state.addressData;
    }
  },
  methods: {
    handleRealClose() {
      this.isShow = false;
    },
    handleShow(m){
      this.isShow=true;
      this.eNum=m
      console.log(this.addressData)
    },
  
  },
  components: {
    Adress
  }
};
</script>

