import Vue from 'vue'
import VueRouter from 'vue-router'
import Account from '../views/account/Account.vue'
import Address from '../views/account/Address.vue'
import Order from '../views/account/Order'
import Home from '../views/Home'
import Nav from '../components/Nav.vue'
import Login from '../views/Login.vue'
import axios from 'axios'
import Footer from '@/components/Footer'
import Goods from '@/views/Goods'
import Cart  from '@/views/Cart.vue'
import CheckOut from '../views/CheckOut.vue'
// import Address from '../views/account/Address.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    components: {
      nav:Nav,
      default:Home,
      footer: Footer
    },
    alias:'/home'
  },
  {
    path: '/goods',
    name: 'goods',
    components: {
        nav: Nav,
        default: Goods,
        footer: Footer
    }
},
{
  path: '/checkout',
  name: 'checkout',
  components: { 
    default:CheckOut
  }

},
  {
    path: '/login',
    name: 'login',
    components: {
      default:Login
    },
  },
  // {
  //   path: '/detail/:idx',//路由形参
  //   name: 'detail',
  //   components: {
  //     nav:Nav,
  //     default:Detail
  //   },
  // },
  {
    path: '/cart',//路由形参
    name: 'cart',
    components: {
      nav:Nav,
      default:Cart
    },
  },
  // {
  //   path: '/checkout',//路由形参
  //   name: 'checkout',
  //   components: {
  //     nav:Nav,
  //     default:Checkout
  //   },
  // },
  {
    path: '/account',
    name: 'account',
    components: {
      nav: Nav,
      default: Account
    },
    children:[
      {
        path:'/',
        component: Order
      },
      {
        path: 'address',
        component: Address
      }
    ],
    beforeEnter: (to, from, next) => {
      // ...
      let token = window.localStorage.getItem('token')
      if (token){
        axios.get('http://localhost:30001/users/token/verify?token=' + token).then((res)=>{
          if(res.data.code===1){
            next()
          }else{
            next({ path: '/login' })
          }
        })
        next()
      }else{
        next({ path: '/login' })
      }
    }
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
