<template>
  <div class="paddingTop outermostShell">
    <div class="positionAbs defaultImg">
      <img src="https://cdn.bestseller.com.cn/assets/pc/ONLY/images/loginBottom.jpg" alt />
    </div>
    <div class="login-box positionAbs">
      <div class="login-wrapper">
        <p class="login-ph">会员登录</p>
        <form method="post" class="login-form">
          <div class="login-init">
            <div class="username marb20 publicFlex">
              <label for="username" class="text-r"></label>
              <input type="text" name="username" placeholder="请输入手机号登录" v-model="userinfo.username" />
              <span class="color-red"></span>
            </div>
          
          </div>
          <div class="login-init">
            <div class="username marb20 publicFlex">
              <label for="username" class="text-r"></label>
              <input type="text" name="upwd" placeholder="请输入你的密码" v-model="userinfo.pwd"/>
              <span class="color-red"></span>
            </div>
          
          </div>
          <input type="button" value="立即登录" class="login-btn" @click="handleLogin" />
          <span class="color-red"></span>
        </form>
        <div class="tip">
          若您尚未注册会员，
          <span class="color-red cursorP">请点击注册</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            userinfo:{
                username:'简承清',
                pwd:'123456'
            }
        }
    },
    methods: {
        handleLogin(){
            // window.localStorage.setItem('login',true);
            // this.$router.push({path:'/'})
            let {username,pwd} = this.userinfo
            let data=`username=${username}&pwd=${pwd}`
            axios.get('http://localhost:30001/users/login/token?'+data).then((res)=>{
                console.log(res)
                window.localStorage.setItem('token',res.data.data);
                this.$router.push({path:'/'})
            })
        }
    },
}
</script>


<style scoped>
    @import 'https://www.only.cn/css/chunk-common.9b32c4bd.css';
    @import 'https://www.only.cn/css/login.40922bae.css';
    .paddingTop {
    padding-top: 0px;
   }
</style>