<script>
import Goodlist from "@/components/Goodlist";
import axios from "axios";
export default {
  data() {
    return {
      goodData: [],
      likeData: [],
      idx: this.$store.state.cidx,
      i: 0,
      icon: 0,
      size: 0,
      bnum: 1,
      shopData:{}
    };
  },
  methods: {
    num(n) {
      this.icon = n;
    },
    addCart() {
      this.shopData.title=this.goodData.goodsName
      this.shopData.cnum=this.bnum
      this.shopData.image='https://cdn.bestseller.com.cn'+ this.goodData.color[this.i].picurls[this.i]
      this.shopData.price=this.goodData.color[this.i].price
      this.shopData.color_name=this.goodData.color[this.i].color
      // this.shopData.isCheck=this.isCheck;
      console.log(this.shopData)
      this.$store.commit("cartAddMut",this.shopData);
    }

  },
  components: {
    Goodlist: Goodlist
  },
  created() {
    axios({
      url: `https://cdn.bestseller.com.cn/detail/ONLY/${this.idx}.json`,
      method: "get",
      headers: {}
    }).then(res => {
      this.goodData = res.data.data;
    });
    axios({
      url:
        "https://www.only.cn/api/goods/dmpRecommendGoods?projectName=detailPage&brand=one&userId=&itemId=118107616S01&brandCode=ONLY",
      method: "get",
      headers: {}
    }).then(res => {
      this.likeData = res.data.data;
    });
  }
};
</script>

