<template>
<div class="address"  >


<input placeholder="收件人姓名" v-model="locData.username"  ><br>
<select name="province"  class="pro" v-model="lv1" @change="handleSelect1">
<option :value="-1">请选择省份</option>
<option v-for="(item,index) in loc1" :key="index"
:value="index">{{item.Name}}</option>
</select>


<select class="city" v-model="lv2"  @change="handleSelect2" >
<option :value="-1">选择市区</option>
<option v-for="(item,index) in loc2" :key="index"
:value="index"> {{item.Name}}</option>
</select>


<select class="cit" v-model="lv3"  >
<option :value="-1">选择区县</option>
<option 
v-for="(item,index) in loc3" :key="index"  :value="index"> {{item.Name}} </option>
</select>
<input type="text" placeholder="街道地址" v-model="locData.address"  ><br>
<input type="text" placeholder="收货人电话" v-model="locData.phone" >
<div>
<div class="botton">
<div  :class="saveState?'':'disabled-btn'" @click="handleSave">保存地址</div>
<div  @click="handleclose" >取消</div>
</div>


</div>

</div>


</template>

<script>
import cityData from '@/assets/js/city.js'

export default {
    props:['editum'],
    data() {
        return {
            loc1:cityData,
            lv1:-1,
            lv2:-1,
            lv3:-1,
            locData:{ 
                "username":"",
                "phone":"",
                "address":"",
                "isDefault":""
            }
        }
    },

    created(){
if(this.editum!=-1){
        this.locData = JSON.parse(JSON.stringify(this.addressData[this.editnum]))
this.lv1=this.addressData[this.editum].loc[0].num
this.lv2=this.addressData[this.editum].loc[1].num
this.lv3=this.addressData[this.editum].loc[2].num
}
    },
    computed: {
addressData(){
return this.$store.state.addressData
},


        loc2(){
            return this.lv1===-1?[]:this.loc1[this.lv1].City
        },
        loc3(){
              return this.lv2===-1?[]:this.loc2[this.lv2].Region
        },
        saveState(){
            let {username,phone,address} = this.locData
            if(username===''||phone===''||address===''||this.lv1===-1||this.lv2===-1||this.lv3===-1){
                return false
            }else{
             
                return true
            }
        }
    },
methods: {
    handleclose(){
        this.$emit('pop-close')
    },
    handleSelect1(){ 
            this.lv2 = this.lv3 = -1;
        },
     handleSelect2(){ 
            this.lv3 = -1;
     },
     handleSave(){
    let newLoc=this.locData;

    newLoc.loc = [
                {
                    "text":this.loc1[this.lv1].Name,
                    "num":this.lv1
                },
                {
                    "text":this.loc2[this.lv2].Name,
                    "num":this.lv2
                },
                {
                    "text":this.loc3[this.lv3].Name,
                    "num":this.lv3
                }
            ]
               this.$emit('pop-close')

     this.$store.commit('updateLocMut',{
         idx:this.editum,
         newLoc
     })
     }
},
}
</script>

<style>
.disabled-btn{
    background-color: skyblue;
}
.address{
    width: 400px;
}
.address input{
    padding-left: 10px;
    width: 300px;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
}

.address select:nth-of-type(1){
margin-top: 10px;
width: 130px;height: 30px;
border: 1px solid
}
.address select:nth-of-type(2){
margin-top: 10px;
margin-left: 8px;
width: 80px;height: 30px;
border: 1px solid
}
.address select:nth-of-type(3){
margin-top: 10px;
width: 80px;height: 30px;
border: 1px solid;
margin-left: 8px;
}
.botton{
    margin-left: 45px;
    margin-top: 10px;
    width: 310px;
    height: 30px;
    display: flex;
    justify-content: space-around;

}
.botton div{
    width: 150px;
    height: 30px;
    line-height: 30px;
    background: black;
    color: white;
}
</style>