<template>
  <div class="PInfoSiteAdd">
    <img
      src="data:image/png;base64,"
      class="PInfoSiteAddCL"
      @click="handleClose"
    />
    <ul>
      <li>
        <p>
          <span>*</span>收货人姓名：
        </p>
        <input type="text" maxlength="20" class="" v-model="locData.username" />
        <label class="none"></label>
      </li>
      <li>
        <p>
          <span>*</span>收货人电话：
        </p>
        <input type="text" class=""  v-model="locData.phone"/>
        <label class="none"></label>
      </li>
      <li>
        <p>
          <span>*</span>省市区：
        </p>
        <select id="province" class="province" v-model="lv1" @change="handleSelect1">
          <option :value="-1">省份/直辖市</option>
           <option 
              v-for="(item,index) in loc1" 
              :key="index" 
              :value="index">{{item.Name}}</option>
        </select>
        <select id="city" class="city" v-model="lv2" @change="handleSelect2">
          <option :value="-1">/直辖市</option>
           <option 
              v-for="(item,index) in loc2" 
              :key="index" 
              :value="index">{{item.Name}}</option>
        </select>
        <select id="area" class="area" v-model="lv3">
          <option :value="-1">请选择区县</option>
                                            <option 
                                            v-for="(item,index) in loc3" 
                                            :key="index" 
                                            :value="index">{{item.Name}}</option>
        </select>
        <label class="none"></label>
      </li>
      <li>
        <p>
          <span>*</span>街道地址：
        </p>
        <input type="text" maxlength="60" class="" v-model="locData.address" />
        <label class="none"></label>
      </li>
      <li>
        <button @click="handleSave">保存</button>
        <button  @click="handleClose">取消</button>
      </li>
    </ul>
  </div>
</template>

<script>
import cityData from '@/assets/js/city.js'
export default {
    props:['editnum'],
    data() {
        return {
            loc1:cityData, //所有省份的数据
            lv1:-1,  //实时对应省份的下标
            lv2:-1, //实时对应城市的下标
            lv3:-1, //实时对应区县的下标
            locData:{ //录入新地址的数据包
                "username":"张三丰",
                "phone":"13888888888",
                "tel":['',''],
                "address":"解放碑的卡卡果果",
                "isDefault":""
            }
        }
    },
    created() {  //根据用户是编辑还是新增的状态数据，处理弹窗默认值
        if(this.editnum!==-1){
            // this.locData = this.addressData[this.editnum] //发生了对象的引用，编辑框的修改会实时反映给vuex
            this.locData = JSON.parse(JSON.stringify(this.addressData[this.editnum]))
            this.lv1 = this.addressData[this.editnum].loc[0].num //初始化省份下标
            this.lv2 = this.addressData[this.editnum].loc[1].num //初始化城市下标
            this.lv3 = this.addressData[this.editnum].loc[2].num //初始化区县下标
        }
    },
    computed: {
        addressData(){ 
            return this.$store.state.addressData
        },
        loc2(){  //一级省份所对应的城市数据
            console.log('计算loc2的方法',this.lv1);
            return this.lv1===-1?[]:this.loc1[this.lv1].City
        },
        loc3(){  //二级级城市所对应的区县数据
            // 方法1：【推荐】
            console.log('计算loc3的方法',this.lv2);
            return this.lv2===-1?[]:this.loc2[this.lv2].Region

            //方法2：
            // if(this.lv1===-1||this.lv2===-1){
            //     return []
            // }else{
            //     return this.loc1[this.lv1].City[this.lv2].Region
            // }
        },
        saveState(){ // 如果这个computed返回true，则可以点击保存按钮
            let {username,phone,address} = this.locData
            if(username===''||phone===''||address===''||this.lv1===-1||this.lv2===-1||this.lv3===-1){
                return false
            }else{
                return true
            }
            
        }
    },
    methods: {
        handleClose(){
            this.$emit('pop-close')
        },
        handleSelect1(){ //一级联动发生切换，重置二三级
            this.lv2 = this.lv3 = -1;
        },
        handleSelect2(){ //二级联动发生切换，重置三级
            this.lv3 = -1;
        },
        handleSave(){
            let newLoc = this.locData;
            newLoc.loc = [
                {
                    "text":this.loc1[this.lv1].Name,
                    "num":this.lv1
                },
                {
                    "text":this.loc2[this.lv2].Name,
                    "num":this.lv2
                },
                {
                    "text":this.loc3[this.lv3].Name,
                    "num":this.lv3
                }
            ]
            this.$emit('pop-close')
            this.$store.commit('updateLocMut',{
                idx:this.editnum,  //标记编辑或新增
                newLoc //新的数据
            })

            
        }
    },
}
</script>

<style scoped>
.PInfoSiteAdd {
  width: 760px;
  height: 400px;
  border: 2px solid #000;
  position: fixed;
  left: 50%;
  top: 50%;
  margin-left: -380px;
  margin-top: -200px;
  z-index: 10;
  background: #fff;
  z-index: 999;
}
.PInfoSiteAdd ul {
  text-align: center;
  text-align: left;
  padding: 3%;
  font-size: 14px;
}
.PInfoSiteAdd ul li p {
  height: 30px;
  line-height: 30px;
}
</style>