<template>
  <div data-v-205c3502 class="claffifyTree">
    <ul data-v-205c3502>
      <li
        data-v-205c3502
        class="firstLevel fontB font14"
        v-for="(item,index) in goodsList"
        :key="index"
        @click="idx = index"
      >
        <div data-v-205c3502>
          <span data-v-205c3502>{{item.classifyName}}</span>
        </div>
        <ul data-v-205c3502 neme="slide-trans" class="secondContent" v-if="isShow=(idx==index)">
          <li data-v-205c3502 class="secondLevel" v-for="(itemx,idx) in item.list" :key="idx">
            <div data-v-205c3502>
              <span data-v-205c3502 class>{{itemx.classifyName}}</span>
            </div>
            <ul data-v-205c3502></ul>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      goodsList: [],
      isShow: false,
      idx: 0
    };
  },
  created() {
    axios({
      url: "https://cdn.bestseller.com.cn/classify/h5/ONLY/h5_list.json",
      method: "get",
      headers: {}
    }).then(res => {
      this.goodsList = res.data.data;
    });
  }
};
</script>

